import React from "react";
import { useSelector, useDispatch } from "react-redux";
import styled from "styled-components";
import { isCheckPoint } from "../../../modules/PointCheckModule";

function NotEnoughCost({ days, totalCredits, credits }) {
  const sponsorStartDate = useSelector(store => store.sponsorStartDateReducer);
  const sponsorEndDate = useSelector(store => store.sponsorEndDateReducer);
  const dispatch = useDispatch();

  //     ,     Math.abs , parseInt NaN
  const creditNeeded =
    parseInt(Math.abs(credits - totalCredits)) || "";
  return (
    <Container>
      <NeedSection>{`${creditNeeded}   `} </NeedSection>
      <DaysSection>
        {sponsorStartDate && sponsorEndDate ? ` ${days} ` : ""}
      </DaysSection>
      <CostSection>{`: ${totalCredits} `}</CostSection>
      <BuyCreditSection
        onClick={() => {
          dispatch(isCheckPoint(true));
        }}
      >
         
      </BuyCreditSection>
    </Container>
  );
}

export default NotEnoughCost;

const Container = styled.div``;

const NeedSection = styled.div`
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: center;
  color: #757575;
  margin-bottom: 15px;
  @media (max-width: 500px) {
    font-size: 12px;
  }
`;

const DaysSection = styled.div`
  font-weight: bold;
  font-size: 13px;
  line-height: 19px;
  text-align: center;
  color: #424242;
`;

const CostSection = styled.div`
  margin-top: 15px;
  height: 27px;
  background: #e64a19;
  border-radius: 4px;
  color: #ffffff;
  font-weight: bold;
  font-size: 13px;
  line-height: 19px;
  text-align: right;
  letter-spacing: 0.08em;
  ${({ theme }) => theme.flex("center", "center", "null")}
  @media (max-width: 500px) {
    font-size: 11px;
  }
`;

const BuyCreditSection = styled.div`
  margin-top: 15px;
  height: 30px;
  background: #212121;
  border-radius: 4px;
  color: #ffffff;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  cursor: pointer;
  ${({ theme }) => theme.flex("center", "center", "null")}
  @media (max-width: 500px) {
    font-size: 11px;
  }
`;
